import React from 'react'
import { useState, useEffect } from 'react';
import Scrollbar from 'material-ui-shell/lib/components/Scrollbar/Scrollbar'
import { useNavigate, useLocation } from 'react-router-dom'
import { useIntl } from 'react-intl'
import { useLocale } from 'base-shell/lib/providers/Locale'
import Stack from '@mui/material/Stack';
import { Device } from '@twilio/voice-sdk'
import DialButton from "./DialButton";
import CallingPad from "./CallingPad";
import CallInput from "./CallInput";
import CallFrom from "./CallFrom";
import useCall from "../../hooks/useCall";
import useSocket from "../../hooks/useSocket";

const AutoDialPad = (props) => {
  const navigate = useNavigate()
  const location = useLocation()
  const [isCalling, setIsCalling] = useState(false);
  // const [isCallStarted, setIsCallStarted] = useState(false);
  const { isCallingState, setIsCallingState, call, setCall, device, setDevice, gatewayNumber } = useCall();
  const { twilioToken } = useSocket();
  const [deviceReady, setDeviceReady] = useState(false);

  useEffect(() => {
    if (twilioToken == null)
      return;
    props.setDialNumber(gatewayNumber)
    async function getFetch() {
      await startUpClient()
    }
    getFetch()
  }, [twilioToken])

  useEffect(() => {
    console.log('device useEffect:', device)
    if (device) {
      console.log('device:', device)
      addDeviceListeners()
      try {
        console.log("device:", device)
        device.register()
        setDeviceReady(true);
      } catch (err) {
        console.log(err)
      }

    }
  }, [device])

  useEffect(() => {
    handleDialing(true)
  }, [deviceReady])

  const startUpClient = async () => {
    try {
      console.log('Got a token.', twilioToken)
      initializeDevice(twilioToken)
    } catch (error) {
      console.log(
        'An error occurred. See your browser console for more information.'
      )
      console.log(error)
    }
  }

  const initializeDevice = (accessToken) => {
    console.log('Initializing device')
    setDevice(
      new Device(accessToken, {
        logLevel: 1,
        // Set Opus as our preferred codec. Opus generally performs better, requiring less bandwidth and
        // providing better audio quality in restrained network conditions.
        codecPreferences: ['opus', 'pcmu'],
      })
    )
  }

  const addDeviceListeners = () => {
    device.on('registered', function () {
      console.log('Twilio.Device Ready to make and receive calls!')
      // callControlsDiv.classList.remove('hide')
    })

    device.on('error', function (error) {
      console.log('Twilio.Device Error: ' + error.message)
    })

    device.on('incoming', handleIncomingCall)

    device.audio.on('deviceChange', updateAllAudioDevices.bind(device))

    // Show audio selection UI if it is supported by the browser.
    if (device.audio.isOutputSelectionSupported) {
      console.log("audioSelectionDiv.classList.remove('hide')") // audioSelectionDiv.classList.remove('hide')
    }
  }

  const handleIncomingCall = (call) => {
    console.log(`Incoming call from ${call.parameters.From}`)
  }

  const updateAllAudioDevices = () => {
    if (device) {
      updateDevices('speakerDevices', device.audio.speakerDevices.get())
      updateDevices('ringtoneDevices', device.audio.ringtoneDevices.get())
    }
  }

  const updateDevices = (selectEl, selectedDevices) => {
    // selectEl.innerHTML = "";

    device.audio.availableOutputDevices.forEach(function (device, id) {
      var isActive = selectedDevices.size === 0 && id === 'default'
      selectedDevices.forEach(function (device) {
        if (device.deviceId === id) {
          isActive = true
        }
      })
      // var option = document.createElement("option");
      // option.label = device.label;
      // option.setAttribute("data-id", id);
      // if (isActive) {
      //   option.setAttribute("selected", "selected");
      // }
      // selectEl.appendChild(option);
    })
  }

  const makeOutgoingCall = async (number) => {
    var params = {
      // get the phone number to call from the DOM
      To: number,
    }

    if (device) {
      console.log(`Attempting to call ${params.To} ...`)

      // Twilio.Device.connect() returns a Call object
      try {
        const twilioCall = await device.connect({ params })
        registerCallEvents(twilioCall)
        // call = twilioCall
        setCall(twilioCall)
      }
      catch (err) {
        console.log(err)
      }

      // add listeners to the Call
      // "accepted" means the call has finished connecting and the state is now "open"
      // call.on('accept', updateUIAcceptedOutgoingCall)
      // call.on('disconnect', updateUIDisconnectedOutgoingCall)
      // call.on('cancel', updateUIDisconnectedOutgoingCall)

      // outgoingCallHangupButton.onclick = () => {
      //   console.log('Hanging up ...')
      //   call.disconnect()
      // }
    } else {
      console.log('Unable to make call.')
    }
  }

  const getUserMedia = () => {
    navigator.getUserMedia =
      navigator.getUserMedia ||
      navigator.webkitGetUserMedia ||
      navigator.mozGetUserMedia
    return new Promise((resolve, reject) => {
      navigator.getUserMedia({ video: true, audio: true }, resolve, reject)
    })
  }

  const registerCallEvents = (call) => {
    // add listeners to the Call
    // "accepted" means the call has finished connecting and the state is now "open"
    call.on('accept', (remoteStream) => {
      // dialpad.setOnCall()
      console.log("registerCallEvents: accept")
      handleReplied()
    })
    call.on('disconnect', () => {
      // dialpad.endCall()
      setIsCalling(false);
      setIsCallingState(false);
      console.log("registerCallEvents: disconnect")
    })
    call.on('cancel', () => {
      // dialpad.endCall()
      console.log("registerCallEvents: cancel")
      handleEndCall()
    })
  }

  const onClickNumber = (number) => {
    props.setDialNumber(props.dialNumber + number)
  }

  const handlePhoneNumber = (number) => {
    console.log(number)
    props.setDialNumber(number);
  }

  const handleDialing = (setCalling) => {
    setIsCalling(setCalling);
    console.log('Calling...', gatewayNumber)
    makeOutgoingCall(gatewayNumber)
    props.setDialNumber('');
  }

  // useEffect(() => {
  // }, [props.dialNumber])

  const handleEndCall = () => {
    try {
      console.log("call:", call)
      call.disconnect()
      console.log("call disconnected");
      setIsCalling(false);
      setIsCallingState(false);
      props.triggerReload(true);
    } catch (e) {
      console.log(e)
    }
  }

  const handleReplied = () => {
    setIsCallingState(true);
  }

  return (
    <Stack justifyContent="center" alignItems="center" spacing={5}>
      <CallFrom />
      {
        !isCalling ? (
          <>
            <CallInput handlePhoneNumber={handlePhoneNumber} phoneNumber={gatewayNumber} handleDialing={handleDialing} />
            <DialButton onClickNumber={onClickNumber} />
          </>
        )
          : <CallingPad isCallStarted={isCallingState} endCall={handleEndCall} user={props.user} call={call} timestamp={new Date().getTime()} />

      }
    </Stack>
  )
}

export default AutoDialPad
