import React, { useEffect, useState, useMemo } from 'react'
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import { useTheme } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Chip from '@mui/material/Chip';
import OutlinedInput from '@mui/material/OutlinedInput';
import Box from '@mui/material/Box';
import { useAppStore } from '../../store/AppStore';
import get from '../../api/info/get';
import post from '../../api/auth/post';
import { loadToken } from '../../api/auth/utils';
import useConference from '../../hooks/useConference';


const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiDialogContent-root': {
        padding: theme.spacing(2),
    },
    '& .MuiDialogActions-root': {
        padding: theme.spacing(1),
    },
}));

function BootstrapDialogTitle(props) {
    const { children, onClose, ...other } = props;

    return (
        <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
            {children}
            {onClose ? (
                <IconButton
                    aria-label="close"
                    onClick={onClose}
                    sx={{
                        position: 'absolute',
                        right: 8,
                        top: 8,
                        color: (theme) => theme.palette.grey[500],
                    }}
                >
                    <CloseIcon />
                </IconButton>
            ) : null}
        </DialogTitle>
    );
}

BootstrapDialogTitle.propTypes = {
    children: PropTypes.node,
    onClose: PropTypes.func.isRequired,
};

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250,
        },
    },
};

const names = [
    'Oliver Hansen',
    'Van Henry',
    'April Tucker',
    'Ralph Hubbard',
    'Omar Alexander',
    'Carlos Abbott',
    'Miriam Wagner',
    'Bradley Wilkerson',
    'Virginia Andrews',
    'Kelly Snyder',
];


function getStyles(name, participants, theme) {

    return {
        fontWeight:
            participants.indexOf(name) === -1
                ? theme.typography.fontWeightRegular
                : theme.typography.fontWeightMedium,
    };
}

export default function CustomizedDialogs(props) {
    const [state, dispatch] = useAppStore();
    const { conferenceTable, setConferenceTable } = useConference();
    const theme = useTheme();

    const [open, setOpen] = React.useState(false);
    const [meetingTime, setMeetingTime] = useState(30);
    const [meetingName, setMeetingName] = useState('Conference 1');
    const [startTime, setStartTime] = useState("2022-11-10T10:30");
    const availableTime = [15, 30, 45, 60, 90];
    const [participants, setParticipants] = React.useState([]);
    const [names, setNames] = useState([]);

    useEffect(() => {
        const getClients = async () => {
            const clients = await get(loadToken(), "api/clients");
            console.log(clients);
            setNames(clients);
        }

        getClients();
    }, [])

    const handleChange = (event) => {
        const {
            target: { value },
        } = event;
        setParticipants(
            // On autofill we get a stringified value.
            typeof value === 'string' ? value.split(',') : value,
        );
    };

    const handleClose = () => {
        console.log(startTime)
        props.setOpen(false);
    };

    const handleSave = async () => {
        let clientIds = [];
        participants.map((item) => {
            clientIds.push({ user: item._id });
        });
        const res = await post("api/conferences", {
            agency: state.currentUser.id,
            name: meetingName,
            starttime: startTime,
            duration: meetingTime,
            participants: clientIds
        })
        console.log(res);

        setParticipants([]);
        setConferenceTable([...conferenceTable, res]);
        props.setOpen(false);
    };

    return (
        <div>
            <BootstrapDialog
                onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={props.isOpen}
                sx={{ minWidth: 500 }}
            >
                <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose} sx={{ minWidth: 500 }}>
                    Create a new conference call
                </BootstrapDialogTitle>
                <DialogContent dividers>
                    <Stack spacing={3}>
                        <Stack>
                            <TextField
                                id="datetime-local"
                                label="Name"
                                type="text"
                                value={meetingName}
                                onChange={(e) => setMeetingName(e.target.value)}
                                sx={{ width: 250 }}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                            />
                        </Stack>
                        <Stack direction="row" spacing={2}>
                            <TextField
                                id="datetime-local"
                                label="Start Time"
                                type="datetime-local"
                                value={startTime}
                                onChange={(e) => setStartTime(e.target.value)}
                                sx={{ width: 250 }}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                            />

                            <FormControl sx={{ m: 1, minWidth: 120 }} size="big">
                                <InputLabel id="demo-simple-select-autowidth-label">Duration</InputLabel>
                                <Select
                                    labelId="demo-simple-select-autowidth-label"
                                    id="demo-simple-select-autowidth-label"
                                    value={meetingTime}
                                    label="Duration"
                                    onChange={(e) => setMeetingTime(e.target.value)}
                                >
                                    {
                                        availableTime.map((item) => (
                                            <MenuItem value={item} > {`${item} mins`}</MenuItem>
                                        ))
                                    }
                                </Select>
                            </FormControl>
                        </Stack>
                        <Stack>
                            <FormControl sx={{ m: 1, minWidth: 300 }}>
                                <InputLabel id="demo-multiple-chip-label">Participants</InputLabel>
                                <Select
                                    labelId="demo-multiple-chip-label"
                                    id="demo-multiple-chip"
                                    multiple
                                    value={participants}
                                    onChange={handleChange}
                                    input={<OutlinedInput id="select-multiple-chip" label="Participants" />}
                                    renderValue={(selected) => (
                                        <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                                            {selected.map((value) => (
                                                <Chip key={value._id} label={value.name} />
                                            ))}
                                        </Box>
                                    )}
                                    MenuProps={MenuProps}
                                >
                                    {names.map((name) => (
                                        <MenuItem
                                            key={name._id}
                                            value={name}
                                            style={getStyles(name, participants, theme)}
                                        >
                                            {name.name}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        </Stack>
                    </Stack>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={handleSave}>
                        Create
                    </Button>
                </DialogActions>
            </BootstrapDialog>
        </div>
    );
}