import React from 'react'
import { useEffect, useState } from 'react';

export default function Counter(props) {

  const useCountdown = () => {
    const countDownDate = props.timestamp;

    const [countDown, setCountDown] = useState(0);

    useEffect(() => {
      const interval = setInterval(() => {
        const current = new Date().getTime() - countDownDate;
        setCountDown(current);
      }, 1000);

      return () => clearInterval(interval);
    }, [countDownDate]);

    return getReturnValues(countDown);
  };

  const getReturnValues = (countDown) => {
    // calculate time left
    const days = Math.floor(countDown / (1000 * 60 * 60 * 24));
    const hours = Math.floor(
      (countDown % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
    );
    const minutes = Math.floor((countDown % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((countDown % (1000 * 60)) / 1000);

    return [days, hours, minutes, seconds];
  };

  const [days, hours, minutes, seconds] = useCountdown();

  return (
    <div>
      <span className="time-1">{hours}</span>
      <span className="time-2"> : </span>
      <span className="time-3">{minutes}</span>
      <span className="time-4"> : </span>
      <span className="time-5">{seconds}</span>
    </div>
  );
}