import React from 'react'
import { useState, useEffect } from 'react';
import Paper from '@mui/material/Paper';
import InputBase from '@mui/material/InputBase';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Button from '@mui/material/Button';
import MenuIcon from '@mui/icons-material/Menu';
import CallIcon from '@mui/icons-material/Call';
import Fab from '@mui/material/Fab';
import DirectionsIcon from '@mui/icons-material/Directions';

const CallInput = (props) => {

  return (
    <Paper
      sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', width: "100%" }}
    >
      <InputBase
        sx={{ ml: 1, flex: 1 }}
        placeholder="Enter a name or number"
        inputProps={{ 'aria-label': 'enter a name or number' }}
        value={props.phoneNumber}
        onChange={(e) => {
          props.handlePhoneNumber(e.target.value)
        }}
      />
      <Fab variant="extended" sx={{ p: '10px', m: 1 }} aria-label="call" color="success" disabled={props.phoneNumber.length == 0} onClick={() => { props.handleDialing(true) }}>
        <CallIcon sx={{ mr: 2, ml: 2 }} />
      </Fab>
    </Paper >
  )
}

export default CallInput
