import { Button } from '@mui/material'
import Page from 'material-ui-shell/lib/containers/Page'
import React, { useEffect, useState, useMemo } from 'react'
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import { styled } from '@mui/material/styles';
import Select from '@mui/material/Select';
import AddIcon from '@mui/icons-material/Add';
import HistoryTable from './HistoryTable';
import CreationDialog from './CreationDialog';
import SpeedDial from '@mui/material/SpeedDial';
import SpeedDialIcon from '@mui/material/SpeedDialIcon';
import SpeedDialAction from '@mui/material/SpeedDialAction';
import FileCopyIcon from '@mui/icons-material/FileCopyOutlined';
import SaveIcon from '@mui/icons-material/Save';
import PrintIcon from '@mui/icons-material/Print';
import ShareIcon from '@mui/icons-material/Share';


const actions = [
  { icon: <FileCopyIcon />, name: 'Copy' },
  { icon: <SaveIcon />, name: 'Save' },
  { icon: <PrintIcon />, name: 'Print' },
  { icon: <ShareIcon />, name: 'Share' },
];


const StyledSpeedDial = styled(SpeedDial)(({ theme }) => ({
  position: 'absolute',
  '&.MuiSpeedDial-directionUp, &.MuiSpeedDial-directionLeft': {
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
  '&.MuiSpeedDial-directionDown, &.MuiSpeedDial-directionRight': {
    top: theme.spacing(2),
    left: theme.spacing(2),
  },
}));


const ConferenceCall = () => {
  const [spacing, setSpacing] = React.useState(12);
  const [number, setNumber] = useState('');
  const [reload, setReload] = useState(false);
  const [dlgOpen, setDlgOpen] = useState(false);

  const agentState = ["Online", "Idle", "Invisible"]

  const [agentStatus, setAgentStatus] = React.useState('Online');

  const handleChange = (event) => {
    setAgentStatus(event.target.value);
  };

  return (
    <>
      <h1>Conference Call</h1>
      <Stack spacing={2}>
        <Stack direction="row">
          <Button variant="contained" startIcon={<AddIcon />} onClick={() => setDlgOpen(true)}>
            New Conference
          </Button>
          {/* <Box sx={{ transform: 'translateZ(0px)', flexGrow: 1 }}>
            <StyledSpeedDial
              ariaLabel="SpeedDial basic example"
              sx={{ position: 'absolute', top: 0, right: 16 }}
              icon={<SpeedDialIcon />}
              direction={"down"}
            >
              {actions.map((action) => (
                <SpeedDialAction
                  key={action.name}
                  icon={action.icon}
                  tooltipTitle={action.name}
                />
              ))}
            </StyledSpeedDial>
          </Box> */}
        </Stack>
        <HistoryTable />
        <CreationDialog isOpen={dlgOpen} setOpen={setDlgOpen} agentStatus={agentStatus} />
      </Stack>

    </>
  )
}
export default ConferenceCall
