import React, {
  useMemo,
  useState,
  useEffect,
  useCallback,
  PropsWithChildren,
} from "react";


export const ConferenceContext = React.createContext(
  {}
);

export const ConferenceContextProvider = ({
  children,
}) => {
  const [conferenceTable, setConferenceTable] = useState();

  return (
    <ConferenceContext.Provider
      value={{
        conferenceTable,
        setConferenceTable
      }}
    >
      {children}
    </ConferenceContext.Provider>
  );
};