import * as React from 'react';
import { useEffect, useState, useMemo } from 'react'
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import ListItemText from '@mui/material/ListItemText';
import ListItem from '@mui/material/ListItem';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import CloseIcon from '@mui/icons-material/Close';
import Slide from '@mui/material/Slide';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Unstable_Grid2';
import Avatar from '@mui/material/Avatar';
import avatar_image from '../../assets/call_avatar.png';
import AutoDial from "../../components/DialPad/AutoDial";


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const bull = (
    <Box
        component="span"
        sx={{ display: 'inline-block', mx: '2px', transform: 'scale(0.8)' }}
    >
        â€¢
    </Box>
);

export default function MeetingDialog(props) {
    const [open, setOpen] = React.useState(false);
    const [number, setNumber] = React.useState('+12024172529');
    const [reload, setReload] = React.useState(false);

    const handleNumber = (num) => {
        console.log(num);
        setNumber(num);
    }


    const handleClose = () => {
        props.setOpen(false);
    };

    return (
        <div>
            <Dialog
                fullScreen
                open={props.isOpen}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar sx={{ position: 'relative' }}>
                    <Toolbar>
                        <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
                            ${`${props.name} (${props.roomid})`}
                        </Typography>
                        <Button autoFocus color="info" onClick={handleClose}>
                            Start
                        </Button>
                        <Button autoFocus color="error" onClick={handleClose}>
                            Leave Meeting
                        </Button>
                    </Toolbar>
                </AppBar>
                <Grid container direction="row">
                    <Grid item xs={10}>
                        <Box sx={{ flexGrow: 1, p: 2, m: 10 }}>
                            <Grid
                                container
                                spacing={4}
                                sx={{
                                    '--Grid-borderWidth': '1px',
                                    borderTop: 'var(--Grid-borderWidth) solid',
                                    borderLeft: 'var(--Grid-borderWidth) solid',
                                    borderColor: 'divider',
                                    '& > div': {
                                        borderRight: 'var(--Grid-borderWidth) solid',
                                        borderBottom: 'var(--Grid-borderWidth) solid',
                                        borderColor: 'divider',
                                    },
                                }}
                            >
                                {props.users?.map((item, index) => (
                                    <Grid key={index} {...{ xs: 12, sm: 6, md: 4, lg: 3 }} minHeight={160}>
                                        <Stack spacing={3} style={{ width: "100%", height: "100%", marginTop: "0%" }} justifyContent="center">
                                            <Stack spacing={3} justifyContent="center" direction="row">
                                                <Avatar src={avatar_image} sx={{ width: 84, height: 84 }} />
                                            </Stack>
                                            <Stack spacing={3} justifyContent="center" direction="row">
                                                <Typography variant="h6">
                                                    {item?.name}
                                                </Typography>
                                            </Stack>

                                            <Stack spacing={3} justifyContent="center" direction="row">
                                                <Typography variant="h6">
                                                    {item?.phone}
                                                </Typography>
                                            </Stack>
                                        </Stack>
                                    </Grid>
                                ))}
                            </Grid>
                        </Box>
                    </Grid>
                    <Grid item xs={2}>
                        <AutoDial triggerReload={setReload} dialNumber={number} setDialNumber={handleNumber} user={"Conference Gateway"} />
                    </Grid>
                </Grid>
            </Dialog>
        </div>
    );
}