import React from 'react'
import { useState, useEffect } from 'react';
import Scrollbar from 'material-ui-shell/lib/components/Scrollbar/Scrollbar'
import { useIntl } from 'react-intl'
import { useLocale } from 'base-shell/lib/providers/Locale'
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import Fab from '@mui/material/Fab';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import '@fontsource/roboto/300.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/500.css';
import '@fontsource/roboto/700.css';
import { NoBackpackSharp } from '@mui/icons-material';

const DialButton = (props) => {

  const onClickNumber = props.onClickNumber;

  const handleClick = (number) => {
    onClickNumber(number);
  }

  const dialContent = [
    [
      { number: '1', alpha: "" },
      { number: '2', alpha: "a b c" },
      { number: '3', alpha: "d e f" },
    ],
    [
      { number: '4', alpha: "g h i" },
      { number: '5', alpha: "j k l" },
      { number: '6', alpha: "m n o" },
    ],
    [
      { number: '7', alpha: "p q r s" },
      { number: '8', alpha: "t u v" },
      { number: '9', alpha: "w x y z" },
    ],
    [
      { number: '*', alpha: "" },
      { number: '0', alpha: "" },
      { number: '#', alpha: "" },
    ]
  ]

  return (
    <Stack spacing={3} style={{ width: "80%" }}>
      {
        dialContent.map((item) => (
          <Stack direction="row" spacing={0}>
            <Grid container rowSpacing={0} >
              {
                item.map((row, index) => (
                  <Grid item key={index} xs={4}>
                    <Button variant="text" color="info" style={{ width: "100%" }} onClick={() => handleClick(row.number)}>
                      <Stack spacing={0}>
                        <Typography variant="h4">
                          {row.number}
                        </Typography>
                        {row.alpha === "" && (<Typography variant="button">&nbsp;</Typography>)}
                        {row.alpha !== "" && (<Typography variant="button">{row.alpha}</Typography>)}
                      </Stack>
                    </Button>
                  </Grid>
                ))
              }
            </Grid>
          </Stack>
        ))
      }
    </Stack>
  )
}

export default DialButton
