import React from 'react'
import { useState, useEffect } from 'react';
import Scrollbar from 'material-ui-shell/lib/components/Scrollbar/Scrollbar'
import { useIntl } from 'react-intl'
import PropTypes from 'prop-types';
import { useLocale } from 'base-shell/lib/providers/Locale'
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import Fab from '@mui/material/Fab';
import { styled } from '@mui/material/styles';
import { useTheme } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import CallIcon from '@material-ui/icons/Call';
import RecordVoiceOverIcon from '@mui/icons-material/RecordVoiceOver';
import DialpadTwoToneIcon from '@mui/icons-material/DialpadTwoTone';
import AddIcon from '@mui/icons-material/Add';
import Avatar from '@mui/material/Avatar';
import get from '../../api/info/get';
import {api} from '../../api';
import { useAppStore } from '../../store/AppStore';
import { loadToken } from '../../api/auth/utils';
import Counter from './CountDown';
import avatar_image from '../../assets/call_avatar.png';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import TextField from '@mui/material/TextField';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import '@fontsource/roboto/300.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/500.css';
import '@fontsource/roboto/700.css';
import SecondaryDialButton from './SecondaryDialButton';

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialogContent-root': {
    padding: theme.spacing(2),
  },
  '& .MuiDialogActions-root': {
    padding: theme.spacing(1),
  },
}));

function BootstrapDialogTitle(props) {
  const { children, onClose, ...other } = props;

  return (
    <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
}

BootstrapDialogTitle.propTypes = {
  children: PropTypes.node,
  onClose: PropTypes.func.isRequired,
};

const CallingPad = (props) => {

  const [displayDial, setDisplayDial] = useState(false);
  const [topMarketerNumber, setTopMarketerNumber] = useState("");
  const [names, setNames] = useState([]);
  const [isAdded, setIsAdded] = useState(false);
  const [state, dispatch] = useAppStore();

  const handleDigit = (digit) => {
    console.log(digit);
    props.call?.sendDigits(digit);
  }

  const handleClose = () => {
    setDisplayDial(false)
  };

  const handleAdd = async () => {
    await api?.auth?.post('api/twilio/addUser', {
      PhoneNumber: topMarketerNumber.phone,
      SessionID: state.currentUser.id
    });
    // props.makeCall(topMarketerNumber.phone);
    setIsAdded(true);
    setDisplayDial(false);
  }

  useEffect(() => {
    const getClients = async () => {
      const clients = await get(loadToken(), "api/clients");
      console.log(clients);
      setNames(clients);
    }

    getClients();
  }, [])

  const handleChange = (event) => {
    setTopMarketerNumber(event.target.value);
    console.log(event.target.value);
  };

  return (
    <Stack spacing={3} style={{ width: "80%", height: "100%", marginTop: "30%" }} justifyContent="center">
      <Stack direction="row" spacing={2} justifyContent="center">
        {
          isAdded && <Stack>
            <Stack spacing={3} justifyContent="center" direction="row">
              <Avatar src={avatar_image} sx={{ width: 84, height: 84 }} />
            </Stack>
            <Stack spacing={3} justifyContent="center" direction="row">
              <Typography variant="h6">
                {topMarketerNumber.name}
              </Typography>
            </Stack>
          </Stack>
        }
        <Stack>
          <Stack spacing={3} justifyContent="center" direction="row">
            <Avatar src={avatar_image} sx={{ width: 84, height: 84 }} />
          </Stack>
          <Stack spacing={3} justifyContent="center" direction="row">
            <Typography variant="h6">
              {props.user}
            </Typography>
          </Stack>
        </Stack>
      </Stack>
      {!props.isCallStarted && (<Stack spacing={3} justifyContent="center" direction="row">
        <Typography variant="h6">
          Calling...
        </Typography>
      </Stack>
      )
      }
      {props.isCallStarted && (<Stack spacing={3} justifyContent="center" direction="row">
        <Typography variant="h6">
          <Counter timestamp={props.timestamp} />
        </Typography>
      </Stack>
      )
      }
      {/* {
        displayDial && (<SecondaryDialButton onClickNumber={handleDigit} />)
      } */}
      <Stack spacing={3} justifyContent="center" direction="row" style={{ marginBottom: '20px' }}>
        {/* <Fab aria-label="record" color="primary" onClick={() => props.record()}>
          <RecordVoiceOverIcon sx={{ mr: 2, ml: 2 }} />
        </Fab> */}
        {props.isCallStarted && (
          <>
            {/* <Fab aria-label="call-end" color="default" onClick={() => setDisplayDial(!displayDial)}>
              <DialpadTwoToneIcon sx={{ mr: 2, ml: 2 }} />
            </Fab> */}
            <Fab aria-label="call-end" color="default" onClick={() => setDisplayDial(!displayDial)}>
              <AddIcon sx={{ mr: 2, ml: 2 }} />
            </Fab>
          </>
        )
        }
        <Fab aria-label="call-end" color="error" onClick={() => props.endCall()}>
          <CallIcon sx={{ mr: 2, ml: 2 }} />
        </Fab>
      </Stack>

      <BootstrapDialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={displayDial}
        sx={{ minWidth: 500 }}
      >
        <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose} sx={{ minWidth: 500 }}>
          Add a new participant
        </BootstrapDialogTitle>
        <DialogContent dividers>
          <Stack direction="row" spacing={5} >
            <FormControl sx={{ m: 1, minWidth: 200 }} size="small">
              <InputLabel id="demo-select-small">Top marketer</InputLabel>
              <Select
                labelId="demo-select-small"
                id="demo-select-small"
                value={topMarketerNumber}
                label="Top marketer"
                onChange={handleChange}
              >
                {names.map((name) => (
                  <MenuItem
                    key={name._id}
                    value={name}
                  >
                    {name.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <TextField id="standard-basic" label=" " variant="standard" value={topMarketerNumber.phone}
              InputProps={{
                readOnly: true,
              }} />
          </Stack>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleAdd}>
            Add
          </Button>
        </DialogActions>
      </BootstrapDialog>
    </Stack >
  )
}

export default CallingPad
