import { api } from '..';
import setAuthToken from '../setAuthToken';

export async function get(token, endpoint) {
  // Dummy object, development only
  if (process.env.REACT_APP_MULTIPASS)
    return {
      nameFirst: 'Leeloo',
      nameLast: 'Dallas',
      phone: '+1234567890',
      email: 'Leeloo.Dallas@Multipass.com',
    };

  try {
    setAuthToken(token);
    const res = await api?.axios?.get(endpoint);
    if (res?.status === 200) {
      const { data } = res;
      log.warn(`${token} -`, data);
      return data;
    }
  } catch (error) {
    log.error(`${token} -`, error);
  }
  return undefined;
}

export default get;
